import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api/api.service';
import { CurrencyDataService } from '../localstore/currency-data.service';

@Component({
  selector: 'app-currency-index',
  templateUrl: './currency-index.component.html',
  styleUrls: ['./currency-index.component.css'],
  providers: [ ApiService, CurrencyDataService ],
})
export class CurrencyIndexComponent implements OnInit {
  public currencyLists = [];
  constructor(private currencyService: ApiService, private currencyDataService: CurrencyDataService) { }

  public async ngOnInit(): Promise<void> {
    this.currencyLists = await this.currencyDataService.getAllData();
  }

}
