import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CurrencyDataService {

  public constructor() { }

  public saveData(currency: object): void {
    const currencyJSON = JSON.stringify(currency);
    localStorage.setItem('currency', currencyJSON);
  }

  public async saveDataByCode(code: string, amount: any): Promise<void> {
    const currencyData = await this.getAllData();
    if (currencyData.length > 0) {
      for (let i = 0; i < currencyData.length; i++ ) {
        if (currencyData[i].code === code) {
          currencyData[i].store = amount;
        }
      }
    }
    this.saveData(currencyData);
  }

  public async getAllData(): Promise<any> {
    const currency = localStorage.getItem('currency');
    return JSON.parse(currency);
  }

  public async getDataByid(id: number): Promise<object> {
    const currencyData = await this.getAllData();
    let currency = {};
    if (currencyData.length > 0) {
      for (let i = 0; i < currencyData.length; i++ ) {
        if (currencyData[i].id === id) {
          currency = currencyData[i];
        }
      }
    }

    return currency;
  }

  public async getDataByCode(code: string): Promise<any> {
    const currencyData = await this.getAllData();
    let currency = {};
    if (currencyData.length > 0) {
      for (let i = 0; i < currencyData.length; i++ ) {
        if (currencyData[i].code === code) {
          currency = currencyData[i];
        }
      }
    }

    return currency;
  }
}
