import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService } from '../api/api.service';
import { CurrencyDataService } from '../localstore/currency-data.service';

@Component({
  selector: 'app-exchange',
  templateUrl: './exchange.component.html',
  styleUrls: ['./exchange.component.css'],
  providers: [ApiService, CurrencyDataService]
})
export class ExchangeComponent implements OnInit {
  public exchangCode: String = 'none';
  public currencyLists: any[] = [];
  public currency: any = {
    firstCurruncy: 'THB',
    secondCurruncy: 'USD',
    current: 0,
    receive: 0
  };

  public realReceive: any = 0;
  public changeAmount: any = 0;

  public constructor(
    public router: Router,
    public route: ActivatedRoute,
    private readonly apiService: ApiService,
    private readonly currencyDataService: CurrencyDataService
  ) {
    this.exchangCode = this.route.snapshot.paramMap.get('code');
  }

  public async ngOnInit() {
    this.currencyLists = await this.getCurrencyList();
    if (this.exchangCode !== 'none') {
      this.currency.firstCurruncy = this.exchangCode;
      this.currency.secondCurruncy = 'THB';
    }
  }

  private async getCurrencyList(): Promise<any[]> {
    const currencyLists = await this.currencyDataService.getAllData();
    return currencyLists;
  }

  public async cal(): Promise<void> {
    console.log('value = ', this.currency.current,
    ' currency = ', this.currency.firstCurruncy,
    ' currency2 = ', this.currency.secondCurruncy);
    const main  = await this.currencyDataService.getDataByCode(this.currency.firstCurruncy);
    const field1 = await this.currencyDataService.getDataByCode(this.currency.secondCurruncy);

    const value = this.currency.current * main.buy;
    this.currency.receive = value / field1.sell;

    if (this.currency.secondCurruncy === 'JPY') {
      this.currency.receive = this.currency.receive * 0.01;
    }
    // console.log(this.currency.receive);
    this.change(this.currency.receive);
  }

  private async change(amount: number): Promise<void> {
    const field1  = await this.currencyDataService.getDataByCode(this.currency.secondCurruncy);
    this.currency.receive = amount.toFixed(2);
    this.realReceive = Math.floor(amount);

    const change = this.currency.receive.substr(-2);
    this.changeAmount = (change * 0.01) * field1.sell;
    this.changeAmount = this.changeAmount.toFixed(0);

    const addData = await this.currencyDataService.getDataByCode(this.currency.firstCurruncy);
    const firstAmount = parseInt(addData.store, 0) + parseInt(this.currency.current, 0);
    this.currencyDataService.saveDataByCode(this.currency.firstCurruncy, firstAmount);

    const deleteData = await this.currencyDataService.getDataByCode(this.currency.secondCurruncy);
    const secondAmount = parseInt(deleteData.store, 0) - parseInt(this.realReceive, 0);
    this.currencyDataService.saveDataByCode(this.currency.secondCurruncy, secondAmount);

    const thbeData = await this.currencyDataService.getDataByCode('THB');
    const thbAmount = parseInt(thbeData.store, 0) - parseInt(this.changeAmount, 0);
    this.currencyDataService.saveDataByCode('THB', thbAmount);
  }

}
