import { Component, OnInit } from '@angular/core';

import { ApiService } from './api/api.service';
import { CurrencyDataService } from './localstore/currency-data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  providers: [ ApiService, CurrencyDataService ],
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  public title = 'GSB-Exchange';
  public currencyList: any[] = [
    'USD', 'AUD', 'CAD', 'CHF', 'CNY',
    'DKK', 'EUR', 'GBP', 'HKD', 'JPY',
    'KRW', 'MYR', 'NOK', 'NZD', 'SEK',
    'SGD', 'TWD'
  ];

  public data: any[] = [];

  constructor (private currencyService: ApiService, private currencyDataService: CurrencyDataService) {

  }

  public ngOnInit(): void {
    this.setCurrencyData();
  }

  private setCurrencyData(): void {
    for (let i = 0; i < this.currencyList.length; i++) {
      this.currencyService.getCurrency(this.currencyList[i]).subscribe(
        rate => this.saveCurrencyData(rate, i),
        error => {
          console.log(error);
        }
      );
    }
  }

  private saveCurrencyData(rate: any, id: number): void {
    const that = this;
    const result = rate.result.data.data_detail[0];
    this.data.push({
      id: id,
      code: result.currency_id,
      name: result.currency_name_th,
      buy: result.buying_transfer,
      sell: result.selling,
      store: 100000,
      icon: 'assets/images/flags/' + result.currency_id.toLowerCase() + '.gif'
    });

    if (this.data.length === this.currencyList.length) {
      const sortData: any[] = [];
      for (let i = 0; i < this.currencyList.length; i++ ) {
        for (let j = 0; j < this.data.length; j++ ) {
          if (this.currencyList[i] === this.data[j].code) {
            sortData.push(this.data[j]);
          }
        }
      }

      sortData.push({
        buy: 1,
        code: 'THB',
        icon: '',
        id: -1,
        name: 'ไทย : บาท (THB)',
        sell: 1,
        store: 100000
      });

      this.currencyDataService.saveData(sortData);
    }
  }

  public getTest() {
    console.log(this.currencyDataService.getAllData());
  }
}
