import { Component, OnInit } from '@angular/core';
import { CurrencyDataService } from '../localstore/currency-data.service';

@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.css']
})
export class InventoryComponent implements OnInit {
  public currencyLists: any[] = [];

  public constructor(private readonly currencyDataService: CurrencyDataService) { }

  public async ngOnInit(): Promise<void> {
    await this.getAllInventory();
  }

  private async getAllInventory(): Promise<any> {
    this.currencyLists = await this.currencyDataService.getAllData();
    console.log(this.currencyLists);
  }

}
