import { Injectable } from '@angular/core';

import { HttpClient, HttpParams } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';

import * as moment from 'moment';

const currencyHttpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json',
    'x-ibm-client-id': '36ac2a9d-4a9b-4671-b591-75f09aa2c7aa',
    'accept': 'application/json'
  })
};

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  constructor(
    private http: HttpClient
  ) {}

  /** GET heroes from the server */
  public getCurrency (currency: string): Observable<any> {
    const today = moment().format('YYYY-MM-DD');
    const yesterday = moment().subtract(5, 'days').format('YYYY-MM-DD');
    const currencyUrl = 'https://apigw1.bot.or.th/bot/public/Stat-ExchangeRate/DAILY_AVG_EXG_RATE_V1/'
                     + '?start_period=' + yesterday + '&end_period=' + today + '&currency=' + currency;
    return this.http.get<any>(currencyUrl, currencyHttpOptions)
      .pipe(
        catchError(this.handleError('getHeroes', []))
      );
  }


  /**
	 * Handle Http operation that failed.
   * Let the app continue.
	 * @param operation
	 * @param result
   * @access private
	 */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      if (error.status === 401) {
        // this.router.navigate(['/system-login']);
        console.log('Access denied');
      }
      // TODO: send the error to remote logging infrastructure
      // console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
